import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Button, Group, Cell, Div, Avatar,} from "@vkontakte/vkui";

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Testing of NerFix Services</PanelHeader>
		{fetchedUser &&
		<Group title="User VK Connect">
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>}

		<Group title="Главная страница">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="persik">
					Какой сейчас курс валют?
				</Button>
				<br/>
				<Button size="xl" level="2" onClick={go} data-to="pers">
					Что там с коронавирусом?
				</Button>
			</Div>
		</Group>

		<Group title="User"></Group>
	</Panel>

	
);

  
Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,	
	share: PropTypes.func,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;