import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import {Group, Header, Cell, Separator} from "@vkontakte/vkui";
import Icon28LogoVkOutline from '@vkontakte/icons/dist/28/logo_vk_outline';
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline';
import Icon28LogoSkype from '@vkontakte/icons/dist/28/logo_skype';
import Icon28SmileOutline from '@vkontakte/icons/dist/28/smile_outline';


import './Pers.css';

const osName = platform();

const Persik = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Зайчик
		</PanelHeader>
		<Group header={<Header mode="secondary">Сепаратор</Header>}>
      <Cell before={<Icon28SmileOutline />}>Уведомления</Cell>
      <Cell before={<Icon28LogoVkOutline />}>Лого VK</Cell>

      <Separator style={{ margin: '12px 0' }} />

      <Cell before={<Icon28BugOutline />}>Жучок (Баг)</Cell>
      <Cell before={<Icon28LogoSkype />}>Скайп вау</Cell>
    </Group>
  </Panel>
);
  


Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};




export default Persik;
